













export default{
	data (){
		return {
			currentIndex: 0,
			footerList: []
		}
	},
	methods:{
		changeItems: function (e){
			this.currentIndex = e.currentTarget.dataset.type;
			this.$emit('currentValue', this.currentIndex)
		}
	},
	mounted (){
		uni.request({
			url: '/api/indexfloor',
			method: 'GET',
			dataType: 'json',
			success: (res) => {
				if(res.data){
					let List = res.data.result.floors[14].data;
					this.footerList = List;
				}
			}
		});
	}
}
